<template>
    <div id="draft-table">

        <!-- 面包屑 start -->
        <el-breadcrumb separator=">" class="breadcrumb">
            <!-- <el-breadcrumb-item>店铺管理</el-breadcrumb-item> -->
            <el-breadcrumb-item :to="{ path: '/goodmenu/good' }">菜品管理</el-breadcrumb-item>
            <el-breadcrumb-item>菜品草稿列表</el-breadcrumb-item>
        </el-breadcrumb>
        <!-- 面包屑 end -->

        <div class="content-box">
            <h3 class="title">菜品草稿列表</h3>

            <!-- table start -->
            <div class="table-content">
                <el-table class="table-list" :data="list" stripe height="680">
                    <el-table-column min-width="80" align="center" label="编号">
                        <template slot-scope="scope">
                            <span>{{ scope.row.food_id}}</span>
                        </template>
                    </el-table-column>
                    <el-table-column min-width="80" align="center" label="图片">
                        <template slot-scope="scope">
                            <img v-if="scope.row.food_picture === ''" src="../images/empty_small_pic_normal.png" alt="">
                            <img v-if="scope.row.food_picture !== ''" 
                            :src="imgbase_url + '/img_get.php?img=1&height=69&width=69&imgname=' + scope.row.food_picture">
                        </template>
                    </el-table-column>
                    <el-table-column min-width="93" align="center" label="名称">
                        <template slot-scope="scope">
                            <span>{{ scope.row.food_name}}</span>
                        </template>
                    </el-table-column>
                    <el-table-column min-width="100" align="center" label="价格">
                        <template slot-scope="scope">
                            <span>{{ scope.row.food_show_price}}</span>
                        </template>
                    </el-table-column>
                    <el-table-column min-width="80" align="center" label="商品类别">
                        <template slot-scope="scope">
                            <span>{{ scope.row.category_name}}</span>
                        </template>
                    </el-table-column>
                    <el-table-column min-width="140" align="center" label="操作">
                        <template slot-scope="scope">
                            <span class="blue-text">
                                <router-link 
                                v-if="scope.row.type === GoodType.DISH" 
                                :to="{path:'/good',query: { foodId:scope.row.food_id}}">
                                    编辑
                                </router-link>
                                <router-link 
                                v-if="scope.row.type === GoodType.ACCESSORY" 
                                :to="{path:'/good/accesseditor',query: { foodId:scope.row.food_id}}">
                                    编辑
                                </router-link>
                                <router-link 
                                v-if="scope.row.type === GoodType.DRINK" 
                                :to="{path:'/good/drinkeditor',query: { foodId:scope.row.food_id}}">
                                    编辑
                                </router-link>
                            </span>
                            <span 
                            @click="dialogDeleteOpen(scope.row.food_id)" 
                            class="red-text" 
                            style="padding-left:10px;padding-top:10px;">删除</span>
                        </template>
                    </el-table-column>
                </el-table>

                <div class="data-empty" v-if="isListEmpty">
                  <div class="img">
                    <img src="../../../../assets/images/empty.png">
                  </div>
                  <div class="text">
                    <span>暂无数据...</span>
                  </div>          
                </div>
            </div>
            <!-- table end -->

            <!-- 分页 starat -->
            <div class="pagination-container">
                <el-pagination 
                @size-change="handleSizeChange" 
                @current-change="handleCurrentChange" 
                :current-page.sync="listQuery.page" 
                :page-sizes="[10,20,40]" 
                :page-size="listQuery.limit" 
                layout="sizes, jumper, prev, pager, next" 
                :total="total">
                </el-pagination>
            </div>
            <!-- 分页 end -->
        </div>

        <!-- 删除提示弹窗 -->
        <el-dialog class="delete-dialog"
        :visible.sync="deleteDialogVisible"
        :show-close="false"
        size="">
          <div class="dialog-title" slot="title"></div>
          <div class="dialog-content">
            <div class="delete-tip">
              <div class="delete-img">
                <img src="../images/question.png" alt="提示图片">
              </div>
              <div class="delete-text">
                确认删除选中菜品
              </div>
            </div>

              <div class="button-group clearfix">
                  <div class="ok-btn left" @click="dialogDelete">确定</div>
                  <div class="cancel-btn left" @click="dialogDeleteCancel">取消</div>
              </div>
          </div>
          
        </el-dialog>
    </div>
</template>

<script>
import { getTableList, menuSave } from "../../api";
import { GoodType, FoodType } from "@/config/cfg";
import { GoodList } from "../../index";
import { ls } from "@/config/pageStore";

export default {
  data() {
    return {
      multipleSelection: [], //存放多选选中数据
      GoodType: GoodType, //food type

      //表格
      list: null,
      total: null,
      listQuery: {
        page: 1,
        limit: 10,
        importance: undefined,
        title: undefined,
        type: undefined,
        sort: "+id"
      },

      imgbase_url: imgbase_url,

      deleteDialogVisible: false, //删除提示弹窗
      deleteId: "", //删除的id

      isListEmpty: true
    };
  },
  created() {
    this.listQuery.limit = ls.getItem("GOODFORMSIZE") || 10;
    this.getList();
  },
  methods: {
    //拉取草稿数据
    getList() {
      let data = {
        foodlist: 1,
        is_draft: 1,
        page_size: this.listQuery.limit,
        page_no: this.listQuery.page
      };

      getTableList(data, resp => {
        if (resp.ret === 0) {
          this.list = resp.data.list || [];
          this.total = resp.data.total;

          //是否为空
          if (this.list.length !== 0) {
            this.isListEmpty = false;
          } else {
            this.isListEmpty = true;
          }

          this.list = this.list.map(item => {
            item["food_picture"] = (item.food_img_list || [])[0] || "";

            //价格
            let foodShowPrice;
            if (item.type === GoodType.ACCESSORY) {
              foodShowPrice = item.food_price;
            } else {
              if ((item.food_price || {}).type === FoodType.YES) {
                foodShowPrice = ((item.food_price.price || [])[0] || {})
                  .original_price;
              } else if ((item.food_price || {}).type === FoodType.NO) {
                let arr = item.food_price.price || [];
                let priceArr = [];

                arr.forEach(item => {
                  priceArr.push(item.original_price);
                });

                let min = GoodList.arrayMin(priceArr);
                let max = GoodList.arrayMax(priceArr);

                foodShowPrice = min + "/" + max;
              }
            }
            item["food_show_price"] = foodShowPrice;
            return item;
          });
        } else {
          console.warn("获取草稿列表数据错误!");
        }
      });
    },

    //分页事件处理，条数和当前页数改变触发
    handleSizeChange(val) {
      this.listQuery.limit = val;
      ls.setItem("GOODFORMSIZE", val, 30);
      this.getList();
    },
    handleCurrentChange(val) {
      this.listQuery.page = val;
      this.getList();
    },

    //删除
    deleteFood(foodId) {
      let foodArr = [];
      foodArr.push(foodId);

      let data = {
        del_food: 1,
        food_id_list: foodArr
      };

      menuSave(data, resp => {
        if (resp.ret === 0) {
          this.getList();
          this.$notify({
            title: "成功",
            message: "操作成功！",
            type: "success"
          });
        } else {
          console.warn("操作失败！");
          this.$notify({
            title: "成功",
            message: "操作失败！",
            type: "error"
          });
        }
      });
    },

    dialogDelete() {
      this.deleteFood(this.deleteId);
      this.deleteDialogVisible = false;
    },
    dialogDeleteCancel() {
      this.deleteDialogVisible = false;
    },
    dialogDeleteOpen(id) {
      this.deleteId = id;
      this.deleteDialogVisible = true;
    }
  }
};
</script>

<style lang="scss" scoped>
@import "src/styles/mixin.scss";

.breadcrumb {
  margin-bottom: 14px;
}

.content-box {
  background: #ffffff;
  box-shadow: 0 2px 4px 0 #becaeb;
}

.title {
  @include fc(14px, #666666);
  background-color: #f6f8fc;
  height: 40px;
  line-height: 40px;
  padding-left: 14px;
}

.table-content {
  position: relative;
  padding: 20px 14px;

  table {
    img {
      @include wh(60px, 60px);
      vertical-align: middle;
      margin: 10px 0;
    }
  }
}

.red-text {
  @include fc(14px, #E7487E);
  cursor: pointer;
}

.blue-text {
  @include fc(14px, #4877e7);
  cursor: pointer;

  a {
    @include fc(14px, #4877e7);
  }
}

.pagination-container {
  text-align: center;
}
.delete-dialog {
  .dialog-title {
    @include wh(540px, 4px);
    background-color: #5a8cff;
    line-height: 4px;
  }

  .dialog-content {
    padding: 80px 75px 50px;

    .delete-tip {
      margin-bottom: 60px;

      .delete-img {
        margin-left: 80px;
        display: inline-block;
        @include wh(50px, 50px);
        img {
          @include wh(50px, 50px);
        }
      }

      .delete-text {
        display: inline-block;
        padding-bottom: 15px;
        padding-left: 20px;
        vertical-align: bottom;
        @include fc(14px, #666);
      }
    }
    .button-group {
      margin-top: 40px;

      div {
        @include wh(160px, 40px);
        border-radius: 4px;
        border: 1px solid #5a8cff;
        color: #5a8cff;
        line-height: 40px;
        text-align: center;
        cursor: pointer;
      }
      .ok-btn {
        margin-right: 60px;
        background-color: #5a8cff;
        color: #fff;
      }
    }
  }
}
.data-empty {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #fff;
  .img {
    @include wh(200px,200px);
    img {
      @include wh(200px, 200px);
    }
  }
  .text {
    @include fc(14px, #9b9b9b);
    width: 200px;
    text-align: center;
    margin-top: 20px;
  }
}
</style>

<style lang="scss">
@import "src/styles/mixin.scss";

#draft-table {
  .el-breadcrumb {
    @include fc(12px, #333333);
  }
  .el-breadcrumb__item:last-child .el-breadcrumb__item__inner {
    color: #4877e7;
  }
  .el-breadcrumb__item__inner:hover {
    color: #4877e7;
  }
  .el-pagination {
    padding-top: 30px;
    padding-bottom: 20px;
  }
  .el-pagination__jump {
    margin-left: 5px;
    margin-right: 15px;
  }
  .el-table td {
    height: 80px;
  }
  .el-dialog__header {
    padding: 0;
  }
  .el-dialog__body {
    padding: 0;
  }
}
</style>


